___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #eee;
  display: flex;
  justify-content: center;
  font-family: sans-serif;
}

li {
  list-style: none;
}

.todo-title-box,
.todo-input-box,
.todo-task-container {
  padding: 15px;
  border: 1px solid rgb(219, 219, 219);
  background-color: #fff;
  gap: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.input {
  font-style: italic;
  border: none;
  padding: 15px;
  outline: none;
  background-color: #fff;
  display: flex;
  align-items: center;
}

.material-icons {
  cursor: pointer;
}

.description {
  outline: none;
}

.container {
  display: flex;
  flex-direction: column;
  min-width: 50vw;
  height: auto;
  padding: 10px;
  border: 1px solid rgb(176, 176, 176);
  margin-top: 5%;
}

.todo-list-box {
  display: flex;
  flex-direction: column;
}

.task {
  display: flex;
  gap: 15px;
}

.clear-btn {
  width: 100%;
  padding: 20px;
  font-size: 1rem;
  background-color: #e9e7e7;
  border: 1px solid rgb(205 205 205);
  color: rgb(97, 95, 95);
  cursor: pointer;
}
